import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { Platform } from '@ionic/angular';
import * as io from 'socket.io-client';
import { TextToSpeech } from '@ionic-native/text-to-speech/ngx';
import { WebView } from '@ionic-native/ionic-webview/ngx';
import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser/ngx';
import { CacheService } from "ionic-cache";
import { ModalController } from '@ionic/angular';
import { Observable } from 'rxjs';
import { PopupqueuePage } from '../popupqueue/popupqueue.page';
@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  socket: any;
  departmentLocal: any;
  ip: any;
  port: any;
  flagname: any;
  queuestart: any = "";
  shotqueuedepartment: any = "";
  monitornumber: any = "";
  saylevel: any = localStorage.getItem('saylevel');
  options: InAppBrowserOptions = {
    location: 'no',//Or 'no' 
    hidden: 'no', //Or  'yes'
    zoom: 'no',//Android only ,shows browser zoom controls 
    hideurlbar: 'no',//Or 'no'
    toolbar: 'no'

  };
  constructor(
    private tts: TextToSpeech,
    private webview: WebView,
    private iab: InAppBrowser,
    private route: Router,
    private cache: CacheService,
    public modalController: ModalController,
    private platform: Platform,
  ) {
    this.departmentLocal = localStorage.getItem('department');
    this.ip = localStorage.getItem('ip');
    this.port = localStorage.getItem('port');
    this.flagname = localStorage.getItem('flagnamecall');
    this.queuestart = localStorage.getItem('queuestart');
    this.shotqueuedepartment = localStorage.getItem('queuedepartment');
    this.monitornumber = localStorage.getItem('monitornumber');
    let ipNode = "http://" + this.ip + ":" + this.port;
    this.socket = io(ipNode, { transports: ["websocket", "polling"] });
  }

  ngOnInit() {
    this.platform.backButton.subscribe(() => {
      this.route.navigate(['/home']);
    });


    this.socket.connect();
    console.log("Code Run In Page");
    this.socket.on("connect", () => {
      console.log("server Online MobileQueue");
    });
    this.setMonitor();
    //
    this.socket.on("readqueue-counter", (data) => {
      //alert(data.department + " = " + " Local => " + this.departmentLocal);
      //
      if (data.department == this.departmentLocal && data.monitornumber == this.monitornumber) {
        //
        /*{
            queue: qnumber,
            channel: channel,
            department: department,
            monitornumber: monitorNumber,
            fullname: null
            flag: 1
        }*/
        //alert(this.departmentLocal + " = " +  data.department + " Q => " + data.queue);
        if (data.queue) {
          //let datas = data.queue;
          let QNumber = data.queue;
          let chanel = data.channel;
          let fullname = data.fullname;
          this.callQ(QNumber, fullname, chanel);//
          /*
          setTimeout(() => {
            this.callQ(QNumber, fullname, chanel);
          }, 2800)
          */
        }
      }
    });
  }

  async setMonitor() {
    const url = "http://" + this.ip + "/rawq/monitor";
    this.iab.create(url, '_self', this.options);
  }

  doRefresh(event) {
    console.log('Begin async operation');
    this.ngOnInit();
    setTimeout(() => {
      console.log('Async operation has ended');
      event.target.complete();
    }, 2000);
  }

  async callQ(QNumber, fullname, chanel) {
    //let qNumber: string = "";
    //if(this.flagname == "1"){
    //qNumber = this.queuestart + " "+ QNumber + " " + this.shotqueuedepartment + "  " + chanel;
    //} else {
      let qNumber;
      if(fullname || fullname != null){
        qNumber = "  " + QNumber + " " + fullname + " " + this.shotqueuedepartment + chanel;
      } else {
        qNumber = "  " + QNumber + " " + this.shotqueuedepartment + chanel;
      }
      
    //}
    //this.presentModal(QNumber,chanel);
    this.tts.speak({
      text: qNumber,
      rate: this.saylevel,
      locale: 'th-TH',
    }).then(() => this.callEnd())
      .catch((reason: any) => console.log(reason));
  }

  //
  async callEnd() {
    this.socket.emit("check-call-counter", { 
      res: 0, 
      department: this.departmentLocal,
      monitornumber: this.monitornumber,
      flag: 0
    });
    this.dismiss();
  }

  async configPage() {
    this.cache.clearAll();
    this.route.navigate(['/main']);
  }

  async presentModal(QNumber, chanel) {

    const modal = await this.modalController.create({
      component: PopupqueuePage,
      cssClass: 'my-custom-class',
      componentProps: {
        'Queuenumber': QNumber,
        'chanel': chanel,
      }
    });
    return await modal.present();
  }

  dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }

}
